import { CHANGE_SEARCH_FIELD, REQUEST_ROBOTS_PENDING, REQUEST_ROBOTS_FAILED, REQUEST_ROBOTS_SUCCESS } from '../constants';


const initialState_search = {searchText: ""}

export const searchReducer = (state=initialState_search, action) => {

	let nextState
	console.log(action)

	switch(action.type) {

		case CHANGE_SEARCH_FIELD:
			nextState = {...state, searchText: action.payload}
			return nextState || state

		default:
			return state
	}
}


const initialState_robots = {isPending: false, robots: [], error: {}}

export const requestRobotReducer = (state=initialState_robots, action={}) => {

	let nextState

	switch(action.type) {

		case REQUEST_ROBOTS_PENDING:
			nextState = {...state, isPending: true}
			return nextState || state



		case REQUEST_ROBOTS_SUCCESS:
			nextState = {...state, isPending: false, robots: action.payload}
			return nextState || state



		case REQUEST_ROBOTS_FAILED:
			nextState = {...state, isPending: false, error: action.payload}
			return nextState || state


		default:
			return state
	}
}


